import HeaderBack from '../Common/HeaderBack';
import CouponNotUsed from '../Common/CouponNotUsed';
import CouponUsed from '../Common/CouponUsed';
import CouponOverdue from '../Common/CouponOverdue';

export default {
  components: { // 
    myHeader: HeaderBack,
    CouponNotUsed,
    CouponUsed,
    CouponOverdue,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back",
        HBC_text:"",
        HBR_icon: "",
        HBR_show: true,
        HBR_type: "couponexplain",
        HBR_show_long:true,
        HBR_text_long:"",
      },
      currentTab: 'CouponNotUsed',// currentTab 
      couponItems:[],
      couponItems1:[],
      couponItems2:[],
      notUsedIsEmpty:false,
      usedIsEmpty:false,
      overdueIsEmpty:false,
    }
  },
  methods: {
    toggleTab: function(name) {
      this.currentTab = name; // name 
    },
    ajax(){
      // mock
      var data = {
        weishiyong:[
          {id:'111',momey:'50',coupon:"50",couponMsg:"",couponStart:"2017.01.01",couponEnd:'2017.02.01'},
          {id:'222',momey:'100',coupon:"100",couponMsg:"500100",couponStart:"2017.01.02",couponEnd:'2017.02.02'},
          {id:'333',momey:'200',coupon:"200",couponMsg:"1000200",couponStart:"2017.01.03",couponEnd:'2017.02.03'},
          {id:'444',momey:'500',coupon:"500",couponMsg:"2000500",couponStart:"2017.01.03",couponEnd:'2017.02.03'},
        ],
        yishiyong:[
          {id:'111',momey:'50',coupon:"50",couponMsg:"",couponStart:"2017.01.01",couponEnd:'2017.02.01'},
          {id:'222',momey:'100',coupon:"100",couponMsg:"500100",couponStart:"2017.01.02",couponEnd:'2017.02.02'},
          {id:'333',momey:'200',coupon:"200",couponMsg:"1000200",couponStart:"2017.01.03",couponEnd:'2017.02.03'},
          {id:'444',momey:'500',coupon:"500",couponMsg:"2000500",couponStart:"2017.01.03",couponEnd:'2017.02.03'},
        ],
        yiguoqi:[
          {id:'111',momey:'50',coupon:"50",couponMsg:"",couponStart:"2017.01.01",couponEnd:'2017.02.01'},
          {id:'222',momey:'100',coupon:"100",couponMsg:"500100",couponStart:"2017.01.02",couponEnd:'2017.02.02'},
          {id:'333',momey:'200',coupon:"200",couponMsg:"1000200",couponStart:"2017.01.03",couponEnd:'2017.02.03'},
          {id:'444',momey:'500',coupon:"500",couponMsg:"2000500",couponStart:"2017.01.03",couponEnd:'2017.02.03'},
        ]
      }

      if(JSON.stringify(data.weishiyong)=='[]'){
        this.notUsedIsEmpty = true;
      } else {
        this.couponItems = data.weishiyong;
      }

      if(JSON.stringify(data.yishiyong)=='[]'){
        this.usedIsEmpty = true;
      } else {
        this.couponItems1 = data.yishiyong;
      }

      if(JSON.stringify(data.yiguoqi)=='[]'){
        this.overdueIsEmpty = true;
      } else {
        this.couponItems2 = data.yiguoqi;
      }
    }
  },
  created () {
    this.ajax();
  }
}



